#container {
    width: 100%;
    display: flex;
    justify-content: center;
}
#main {
    background: rgb(187, 187, 187);
    width: 700px;
    height: 100vh;
    display: flex;
    /* justify-content: center; */
    flex-direction: column;

}

#input {
    margin-top: 10px;
    border-radius: 10px;
    background: #fff;
    width: 400px;
    height: 30px;
    align-self: center;
}

#btns {
    margin-top: 4px;
    align-self: center;
    width: 400px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.btn {
    border-radius: 10px;
    height: 30px;
    background-color: aquamarine;

}

#clear {
    background: rgb(255, 97, 97);
}

#count {
    margin-top: 12px;
    align-self: center;
    font-size: 1.3em;
}

#todos{
    width: 500px;
    border: 1px solid black;
    align-self: center;
    border-radius: 10px;
    margin: 1px;
}

.label {
    display: flex;
    width: 100%;
    justify-content: center;
    margin-top: 8px;
    font-size: 1.2em;

}

#check{
    width: 20px;
    height: 20px;
    border: 2px solid black;

}